@use "./variables.scss";

@mixin form-margin-padding {
  margin: 0;
  padding: 0;
}

.form {
  @media (min-width: variables.$tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.form__subtitle {
  text-transform: uppercase;
  padding: 16px 16px 0 16px;
}

.form__box {
  display: flex;
  flex-direction: column;
  border: none;
  margin-bottom: 49px;

  @include form-margin-padding;
}

.form__box--name {
  margin-bottom: 64px;

  @media (min-width: variables.$tablet-width) {
    margin-right: 43px;
  }
}

.form__box--contacts {
  margin-bottom: 59px;

  @media (min-width: variables.$tablet-width) {
    margin-left: 43px;
  }
}

.form__feeling {
  @include form-margin-padding;

  display: grid;
  border: none;
  margin-bottom: 59px;

  @media (min-width: variables.$tablet-width) {
    grid-template-columns: repeat(2, 1fr);
    grid-column: 1/-1;
    margin-bottom: 58px;
  }

  @media (min-width: variables.$desktop-width) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.form__places {
  @include form-margin-padding;

  display: grid;
  border: none;
  margin-bottom: 59px;

  @media (min-width: variables.$tablet-width) {
    grid-template-columns: repeat(2, 1fr);
    grid-column: 1/-1;
    margin-bottom: 58px;
  }

  @media (min-width: variables.$desktop-width) {
    grid-template-columns: repeat(4, 1fr);
  }
}

.form__emotions {
  margin: 0 auto;
  padding: 0;
  border: none;
  margin-bottom: 59px;

  @media (min-width: variables.$tablet-width) {
    grid-column: 1/-1;
  }
}

.form__textarea {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  color: #000000;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  height: 150px;
  width: 100%;
  margin: 0;
  border: none;
  background-color: #f2f2f2;
  margin-top: 20px;
  margin-bottom: 56px;
  padding: 16px;

  @media (min-width: variables.$tablet-width) {
    width: 648px;
  }

  @media (min-width: variables.$desktop-width) {
    width: 768px;
  }

  &::placeholder {
    color: rgba(0, 0, 0, 0.3);
  }
}

.form__input {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  color: #000000;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  border: none;
  background-color: #f2f2f2;
  padding: 16px;

  &:hover {
    color: #000000;
    background-color: #e5e5e5;
    border: none;
  }

  &:active {
    background: #ffffff;
    outline: 4px solid #e5e5e5;
    border: none;
  }

  &::placeholder {
    color: rgba(0, 0, 0, 0.3);
  }
}

.form__control {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.form__button {
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  color: #ffffff;
  background-color: #81b3d2;
  text-transform: uppercase;
  border: none;
  padding: 16px 48px;

  @media (min-width: variables.$tablet-width) {
    padding: 16px 71px;
  }

  @media (min-width: variables.$desktop-width) {
    padding: 16px 101px;
  }
}

.form__wrapper {
  @media (min-width: variables.$tablet-width) {
    display: flex;
    justify-content: space-between;
  }
}

.form__comment {
  font-size: 12px;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 0.6);

  @media (min-width: variables.$tablet-width) {
    font-size: 14px;
  }
}
